<script>
  import { onMount } from 'svelte';

  let grow = false;
  let shrink = false;
  let text;

  const totalTime = 7500;
  const breatheTime = (totalTime / 5) * 2;
  const holdTime = totalTime / 5;

  const breathAnimation = () => {
    text = 'Breathe In!';
    grow = true;
    shrink = false;

    setTimeout(() => {
      text = 'Hold';

      setTimeout(() => {
        text = 'Breathe Out!';
        grow = false;
        shrink = true;
      }, holdTime);
    }, breatheTime);
  };

  setInterval(breathAnimation, totalTime);

  onMount(() => {
    breathAnimation();
  });
</script>

<style>
  main {
    min-height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    height: 300px;
    width: 300px;
    position: relative;
    transform: scale(1);
  }

  .circle {
    background-color: #010f1c;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  .gradient-circle {
    background: conic-gradient(
      #55b7a4 0%,
      #4ca493 40%,
      #fff 40%,
      #fff 60%,
      #336d62 60%,
      #2a5b52 100%
    );
    height: 320px;
    width: 320px;
    z-index: -2;
    border-radius: 50%;
    position: absolute;
    top: -10px;
    left: -10px;
  }

  .pointer {
    background-color: #fff;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    display: block;
  }

  .pointer-container {
    position: absolute;
    top: -40px;
    left: 140px;
    width: 20px;
    height: 190px;
    animation: rotate 7.5s linear forwards infinite;
    transform-origin: bottom center;
  }

  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }

  .container.grow {
    animation: grow 3s linear forwards;
  }

  @keyframes grow {
    from {
      transform: scale(1);
    }

    to {
      transform: scale(1.2);
    }
  }

  .container.shrink {
    animation: shrink 3s linear forwards;
  }

  @keyframes shrink {
    from {
      transform: scale(1.2);
    }

    to {
      transform: scale(1);
    }
  }
</style>

<main>
  <h1>Relaxer</h1>

  <div class="container" id="container" class:grow class:shrink>
    <div class="circle" />

    <p id="text">{text}</p>

    <div class="pointer-container"><span class="pointer" /></div>

    <div class="gradient-circle" />
  </div>
</main>
